body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.truyen-div {
  text-align:justify;
  margin-top: 80px;
}
.chon-trang{
  width: 50px;
}
.truyen-div-dropdown {
  text-align:center;
}
.truyen-PDF {
  margin-top: 80px;
}
.document-container {
  background-color: inherit !important;
  width: inherit !important;
}
.react-pdf__Page__textContent {
  background-color: inherit !important;
  color: inherit !important;
}
.phim-may-tinh {
  margin-top: 60px;
}

.tenCuaChu {
  font-size: 30px;
}
.menu-hien-ra {
  opacity: 1;
  -webkit-transform: translate(0px,0px) scale(1) rotate(0deg);
          transform: translate(0px,0px) scale(1) rotate(0deg);
  -webkit-transition: opacity 1s, -webkit-transform 1s;
  transition: opacity 1s, -webkit-transform 1s;
  transition: transform 1s, opacity 1s;
  transition: transform 1s, opacity 1s, -webkit-transform 1s;
}

.menu-dau-di {
  opacity: 0;
  -webkit-transform: translate(100vw,100vh) scale(0) rotate(800deg);
          transform: translate(100vw,100vh) scale(0) rotate(800deg);
  -webkit-transition: opacity 1s, -webkit-transform 1s;
  transition: opacity 1s, -webkit-transform 1s;
  transition: transform 1s, opacity 1s;
  transition: transform 1s, opacity 1s, -webkit-transform 1s;
}

.lop-duoi-menu-hien-ra {
  width: 100vw;
  height: 100vh;
  background-color: black;
  opacity: 0.5;
  visibility: visible;
  position: fixed;
  top: 0;
  left: 0;
}

.lop-duoi-menu-dau-di {
  visibility: hidden; 
}

